.site {
    &-title {
        float: left;
        padding: 0;

        > h1 {
            font-size: (16rem/16);
            font-weight: normal;
            margin: 0 0 0 0;
            text-transform: capitalize;

            > a {
                color: $header-text-color;
            }
        }
    }
    &-location {
        margin: -.4rem 0 0 0;

        > a {
            color: $header-text-color;
            font-size: (11rem/16);
        }
    }
    &-utilityBar {
        position: relative;

        nav {
            float: right;
            margin: 0 0 0 1em;
            z-index: 7;
        }
    }
}
